@import url(https://fonts.googleapis.com/css?family=Niramit);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: 'Niramit', sans-serif;

}

#root {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
.root-container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: url(https://images.pexels.com/photos/97075/pexels-photo-97075.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940);
  background-size: cover;
  background-position: center;
}
.box-controller {
  border-radius: 4px;
  background: white;
  visibility: visible;
  display: flex;
  max-width: 24em;
  min-width: 20em;
  height: 35px;
  box-shadow: 0px 0px 20px 0px rgba(15, 15, 15, 0.2);
  margin-bottom: 30px;
  align-items: center;
  -webkit-transition: visibility 0.5s ease-out;
  transition: visibility 0.5s ease-out;
}

.box-cu {
  border-radius: 4px;
  background: white;
  padding: 17px;
  max-width: 24em;
  min-width: 18em;
  box-shadow: 0px 0px 20px 0px rgba(15, 15, 15, 0.2);
  margin-bottom: 30px;
  align-items: center;
  -webkit-transition: visibility 0.5s ease-out;
  transition: visibility 0.5s ease-out;
}

.controller {
  flex: 1 1;
  text-align: center;
  height: 100%;
  line-height: 2;
  cursor: pointer;
}

.selected-controller {
  -webkit-transition: border 0.5s ease-out;
  transition: border 0.5s ease-out;
  border-bottom: 2px solid;
}

.box-container {
  display: flex;
  flex-direction: column;
  max-width: 24em;
  min-width: 21em;
  box-shadow: 0px 0px 20px 0px rgba(15, 15, 15, 0.2);
  border-radius: 6px;
  padding: 24px;
}

.inner-container {
  -webkit-transition: visibility 0.2s ease-out;
  transition: visibility 0.2s ease-out;
}

.inner-container.show {
  visibility: visible;
}

.header {
  text-align: center;
  padding: 5px;
  margin-bottom: 17px;
  font-size: 22px;
  border-bottom: 2px solid;
}

.box {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.input-group {
  display: flex;
  flex-direction: column;
  margin-top: 5px;
  margin-bottom: 5px;
}

.login-label {
  font-size: 20px;
  font-weight: 600;
  padding-left: 5px;
}

.login-input {
  border-radius: 3px;
  border: 1px solid;
  -webkit-transition: border 0.4s ease-out;
  transition: border 0.4s ease-out;
  padding: 13px;
  font-size: 19px;
  color: #3e3e42;
  background: rgba(15, 15, 15, 0.01);
}

.login-input:hover {
  border: 2px solid;
}

.login-input:focus {
  border: 2px solid #2727b6;
  box-shadow: 0px 0px 20px rgba(15, 15, 15, 0.2);
}

.login-input::-webkit-input-placeholder {
  font-size: 16px;
  color: rgba(46, 213, 116, 0.839);
  color: rgba(15, 15, 15, 0.4);
}

.login-input::-ms-input-placeholder {
  font-size: 16px;
  color: rgba(46, 213, 116, 0.839);
  color: rgba(15, 15, 15, 0.4);
}

.login-input::placeholder {
  font-size: 16px;
  color: rgba(46, 213, 116, 0.839);
  color: rgba(15, 15, 15, 0.4);
}

.login-btn {
  padding: 2px 30px;
  border: 0;
  font-size: 18px;
  border-radius: 3px;
  background-color: rgba(15, 15, 15, 0.03);
  margin-top: 20px;
  border: 2px solid #2727b6;
  -webkit-transition: background-color 0.3s ease-out;
  transition: background-color 0.3s ease-out;
  cursor: pointer;
}

.login-btn:focus {
  background-color: #2727b6;
  color: white;
}

.App {
  background: url(https://images.pexels.com/photos/97075/pexels-photo-97075.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940);
  background-size: cover;
  background-position: center;
  height: 100vh;
}
.change{
  background: rgb(15, 103, 171);
  padding: 5%;
  color:white;
}

.cuadro{
  width: 100%;
  height: 350px;
  overflow:scroll;
}
.hidden{
    display: none;
}
